<template>
  <div class="record-box">
    <div class="record-bar">
      <van-nav-bar
        :title="$t('FundingRecords')"
        left-arrow
        @click-left="linkHome"
      />
    </div>
    <div class="record-tab">
      <van-tabs v-model="tradeTab" color="#1988FF" title-active-color="#1988FF" title-inactive-color="#222222" swipeable animated :border="false" @change="changeTab">
        <van-tab :title="$t('FundingRecords')">
          <div class="capital-box">
            <van-list
              v-model="loading"
              :finished="finished"
              :finished-text="$t('NoMore')"
              @load="onLoadList"
            >
            <div class="capital" >
              <div v-for="(item,index) in CapitalRecordList" :key="item.id">
                <div>
                  <div class="class">{{item.comments}}</div>
                  <div class="time">{{item.time}}</div>
                </div>
                <div style="text-align: right">
                  <div class="class" style="color: #FD4F50">${{(item.balanceAfter-item.balanceBefore).toFixed(1)}}</div>
                  <div class="time">{{$t('balance')}}：{{item.balanceAfter}}</div>
                </div>
              </div>
            </div>
            </van-list>
          </div>
        </van-tab>
        <van-tab :title="$t('RechargeRecord')">
          <div class="capital-box">
            <van-list
              v-model="RechargeLoading"
              :finished="RechargeFinished"
              :finished-text="$t('NoMore')"
              @load="RechargeScroll"
            >
            <div class="capital" >
              <div v-for="(item,index) in RechargeList" :key="item.id">
                <div style="width: 40%">
                  <div class="class" v-if="item.orderDesc === $t('orderDesc1') || item.orderDesc === $t('orderDesc2')">{{item.orderDesc}}</div>
                  <div class="class" v-else>{{item.status}}</div>
                  <div class="time" style="margin: 0">{{item.orderTime}}</div>
                </div>
<!--                <div style="width: 42%">-->
<!--                  <div v-if="item.orderDesc === '充值'"><span style="color: #8C8B8B;">充值金额：</span>￥{{item.orderAmount}}</div>-->
<!--                  <div class="time" v-if="item.orderDesc === '充值'">汇率：<span style="color: #101010;">{{item.rate}}</span></div>-->
<!--                  <div v-else style="text-align: center"><span style="color: #8C8B8B;">类型：</span>{{item.orderDesc}}</div>-->
<!--                  <div class="time" v-show="item.orderDesc != '充值'"></div>-->
<!--                </div>-->
                <div style="text-align: right;width: 60%">
                  <div class="class" style="color: #FD4F50">+${{item.Dollar}}</div>
                  <div class="time"></div>
                </div>
              </div>
            </div>
            </van-list>
          </div>
        </van-tab>
        <van-tab :title="$t('WithdrawalsRecord')">
          <div class="capital-box">
            <van-list
              v-model="CashOutLoading"
              :finished="CashOutFinished"
              :finished-text="$t('NoMore')"
              @load="CashOutScroll"
            >
            <div class="capital cash" >
              <div v-for="(item,index) in CashOutList" :key="item.id" :class="item.class?'class-show':''">
                <div style="width: 70%">
                  <div class="class">{{item.status}}</div>
                  <div v-if="!item.class">到账金额：￥{{item.Dollar}}</div>
                  <div v-if="!item.class" class="bank">{{item.applyBankName}}({{item.applyBankAccount}})</div>
                  <div class="time">{{item.applyTime}}</div>
                </div>
<!--                <div style="width: 10%">-->
<!--                  <div v-if="!item.class" class="time">汇率：<span style="color: #101010;">{{item.rate}}</span></div>-->
<!--                  <div v-else style="text-align: center"><span style="color: #8C8B8B;">类型：</span>{{item.class}}</div>-->
<!--                  <div class="time" v-show="item.class"></div>-->
<!--                </div>-->
                <div style="text-align: right;width: 30%">
                  <div class="class" style="color: #FD4F50">-${{item.money}}</div>
                  <div v-if="!item.class" class="time">汇率：<span style="color: #101010;">{{item.rate}}</span></div>
                  <div class="time" v-show="item.class"></div>
                </div>
              </div>
            </div>
            </van-list>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: 'capitalRecord',
  data () {
    return {
      // 默认显示Tab页
      tradeTab: 0,
      uid:'',
      token:'',
      discUrl:'',
      CapitalRecordList:[],
      loading: false,
      finished: false,
      // 当前页码
      currentPage:0,
      oldCPage1:0,
      oldCPage2:0,
      oldCPage3:0,

      RechargeList:[],
      RechargeLoading:false,
      RechargeFinished:false,
      RechargePage:0,


      showRecharge:false,
      CashOutList:[],
      CashOutLoading:false,
      CashOutFinished:false,
      CashOutPage:0,

      showCashOut:false
    }
  },
  created () {
    this.tradeTab = this.$route.params.tradeTab
    this.uid = localStorage.getItem('uid')
    this.token = localStorage.getItem('token')
    this.discUrl = localStorage.getItem('discUrl')
    // this.getroadList()
    // this.setRecharge()
    // this.setCashOut()
  },
  methods: {
    onLoadList() {
      // if(this.oldCPage1){
      //   this.oldCPage1 = false
      // }else {
      this.getroadList()

      this.currentPage++

      // }
    },
    getroadList() {
      let that = this
      console.log(this.currentPage)
      this.$axios.get('http://'+this.discUrl+':7777/api/conpay/moneychange?userId='+this.uid+'&token='+this.token+'&page='+this.currentPage).then(res => {
        if (res.data.code === 0 && res.data.object.data != null) {
          let rows = res.data.object.data
          console.log(rows)
          for(let i=0;i<rows.length;i++){
            let thisTowDate = new Date(rows[i].time);
            let currentTowTime = thisTowDate.getFullYear() + '-' + (thisTowDate.getMonth() + 1).toString().padStart(2,'0') + '-' + (thisTowDate.getDate()).toString().padStart(2,'0') + ' ' + (thisTowDate.getHours()).toString().padStart(2,'0') + ':' + (thisTowDate.getMinutes()).toString().padStart(2,'0');
            rows[i].time = currentTowTime
            // 1: 扣除保证金 2：返还保证金 3：扣除手续费 4：扣除仓息  5：平仓结算  6：充值：7：取款  8: 红冲 9： 蓝补 10: 点差收益 11: 手续费分成  12 盈亏分成 13 仓息分成 14 点差分成  15：赠金分成  16: 对冲平仓结算 17：出入金手续费
            // comments1:'扣除保证金',
            //   comments2:'返还保证金',
            //   comments3:'扣除手续费',
            //   comments4:'扣除仓息',
            //   comments5:'平仓结算',
            //   comments6:'充值',
            //   comments7:'取款',
            //   comments8:'红冲',
            //   comments9:'蓝补',
            //   comments10:'点差收益',
            //   comments11:'手续费分成',
            //   comments12:'盈亏分成',
            //   comments13:'仓息分成',
            //   comments14:'点差分成',
            //   comments15:'赠金分成',
            //   comments16:'对冲平仓结算',
            //   comments17:'出入金手续费',
            // if(rows[i].comments === ''){
              switch (rows[i].type) {
                case 1:
                  rows[i].comments = that.$t('comments1');
                  break;
                case 2:
                  rows[i].comments = that.$t('comments2');
                  break;
                case 3:
                  rows[i].comments = that.$t('comments3');
                  break;
                case 4:
                  rows[i].comments = that.$t('comments4');
                  break;
                case 5:
                  rows[i].comments = that.$t('comments5');
                  break;
                case 6:
                  rows[i].comments = that.$t('comments6');
                  break;
                case 7:
                  rows[i].comments = that.$t('comments7');
                  break;
                case 8:
                  rows[i].comments = that.$t('comments8');
                  break;
                case 9:
                  rows[i].comments = that.$t('comments9');
                  break;
                case 10:
                  rows[i].comments = that.$t('comments10');
                  break;
                case 11:
                  rows[i].comments = that.$t('comments11');
                  break;
                case 12:
                  rows[i].comments = that.$t('comments12');
                  break;
                case 13:
                  rows[i].comments = that.$t('comments13');
                  break;
                case 14:
                  rows[i].comments = that.$t('comments14');
                  break;
                case 15:
                  rows[i].comments = that.$t('comments15');
                  break;
                case 16:
                  rows[i].comments = that.$t('comments16');
                  break;
                case 17:
                  rows[i].comments = that.$t('comments17');
                  break;
              }
            // }
          }
          if (rows === null || rows.length === 0) {
            // 加载结束
            that.finished = true
            return
          }
          if (rows.length < 20) {
            // 最后一页不足10条的情况
            that.finished = true
          }
          // 处理数据
          if (that.currentPage === 0) {
            that.CapitalRecordList = rows
          } else {
            that.CapitalRecordList = that.CapitalRecordList.concat(rows)
          }
        }else {
          that.finished = true
        }
      }).finally(() => {
        that.loading = false
      })
    },

    RechargeScroll(event){
      // if(this.oldCPage2){
      //   this.oldCPage2 = false
      // }else {
      this.setRecharge()

      this.RechargePage++

      // }
    },
    setRecharge(a){
      let that = this
      console.log(this.RechargePage,'RechargePage')
      // /api/conpay?token=""&userId=""&page=&pageSize=
      this.$axios.get('http://'+this.discUrl+':7777/api/conpay?userId='+this.uid+'&token='+this.token+'&page='+this.RechargePage).then(res =>{
        console.log(res)
        if(res.data.code === 0 && res.data.object.data != null){
          let rows = res.data.object.data
          for(let i=0;i<rows.length;i++){
            let thisTowDate = new Date(res.data.object.data[i].orderTime);
            let currentTowTime = thisTowDate.getFullYear() + '-' + (thisTowDate.getMonth() + 1).toString().padStart(2,'0') + '-' + (thisTowDate.getDate()).toString().padStart(2,'0') + ' ' + (thisTowDate.getHours()).toString().padStart(2,'0') + ':' + (thisTowDate.getMinutes()).toString().padStart(2,'0');
            rows[i].orderTime = currentTowTime
            rows[i].Dollar =  (rows[i].orderAmount/ rows[i].rate).toFixed(2)
            // 0: 等待支付，1:代收成功  2: 代收失败   4：系统充值成功
            // status1:'等待支付',
            //   status2:'充值成功',
            //   status3:'充值失败',
            //   status4:'充值成功',
            switch (rows[i].status) {
              case 0:
                rows[i].status = that.$t('status1');
                break;
              case 1:
                rows[i].status = that.$t('status2');
                break;
              case 2:
                rows[i].status = that.$t('status3');
                break;
              case 4:
                rows[i].status = that.$t('status4');
                break;
            }
            switch (rows[i].orderDesc) {
              // orderDesc1:'System recharge',
              //   orderDesc2:'System transfer',
              case '6':
                rows[i].orderDesc = that.$t('orderDesc1')
                break;
              case '9':
                rows[i].orderDesc = that.$t('orderDesc2');
                break;
            }
          }
          if (rows == null || rows.length === 0) {
            // 加载结束
            that.RechargeFinished = true
            return
          }
          if (rows.length < 20) {
            // 最后一页不足10条的情况
            that.RechargeFinished = true
          }
          // 处理数据
          if (that.RechargePage === 0) {
            that.RechargeList = rows
            console.log(that.RechargeList)
          } else {
            that.RechargeList = that.RechargeList.concat(rows)
            console.log(that.RechargeList)
          }
        }else {
          that.RechargeFinished = true
        }
      }).finally(() => {
        that.RechargeLoading = false
      })
    },

    CashOutScroll(event){
      // if(this.oldCPage3){
      //   this.oldCPage3 = false
      // }else {
      this.setCashOut()

      this.CashOutPage++


      // }

    },
    setCashOut(a){
      // /api/conpay?token=""&userId=""&page=&pageSize=
      console.log(this.CashOutPage,'CashOutPage')
      let that = this
      this.$axios.get('http://'+this.discUrl+':7777/api/conpay/out?userId='+this.uid+'&token='+this.token+'&page='+this.CashOutPage).then(res =>{
        console.log(res)
        if(res.data.code === 0 && res.data.object.data != null){
          let rows = res.data.object.data
          for(let i=0;i<rows.length;i++){
            let thisTowDate = new Date(rows[i].applyTime);
            let currentTowTime = thisTowDate.getFullYear() + '-' + (thisTowDate.getMonth() + 1).toString().padStart(2,'0') + '-' + (thisTowDate.getDate()).toString().padStart(2,'0') + ' ' + (thisTowDate.getHours()).toString().padStart(2,'0') + ':' + (thisTowDate.getMinutes()).toString().padStart(2,'0');
            rows[i].applyTime = currentTowTime
            rows[i].applyBankAccount = rows[i].applyBankAccount.substr(rows[i].applyBankAccount.length-4)
            rows[i].Dollar =  (rows[i].money * rows[i].rate).toFixed(2)
            // 0：待审核， 1：审核通过 2：驳回 3:撤销
            // statu1:'审核通过',
            //   statu2:'驳回',
            //   statu3:'撤销',
            //   statu4:'系统调出',
            switch (rows[i].status) {
              case 0:
                rows[i].status = that.$t('dRealName');
                break;
              case 1:
                rows[i].status = that.$t('statu1');
                break;
              case 2:
                rows[i].status = that.$t('statu2');
                break;
              case 3:
                rows[i].status = that.$t('statu3');
                break;
              case 8:
                rows[i].status = that.$t('statu1');
                rows[i].class = that.$t('statu4');
                break;
            }
          }
          if (rows === null || rows.length === 0) {
            // 加载结束
            that.CashOutFinished = true
            return
          }
          if (rows.length < 20) {
            // 最后一页不足10条的情况
            that.CashOutFinished = true
          }
          // 处理数据
          if (that.CashOutPage === 0) {
            that.CashOutList = rows
          } else {
            that.CashOutList = that.CashOutList.concat(rows)
          }
        }else {
          that.CashOutFinished = true
        }
      }).finally(() => {
        that.CashOutLoading = false
      })
    },

    changeTab(a) {
      let that = this
      if (a === 0) {
        // that.CapitalRecordList=[]
        // this.currentPage = 0
        // this.RechargePage = 0
        // that.RechargeList=[]
        // this.CashOutPage = 0
        // that.CashOutList=[]
        // console.log(this.currentPage)
        // this.getroadList()
        // this.oldCPage1 = true
      }
      if (a === 1) {
        // this.currentPage = 0
        // that.CapitalRecordList=[]
        // this.RechargePage = 0
        // that.RechargeList=[]
        // this.CashOutPage = 0
        // that.CashOutList=[]
        // console.log(this.RechargePage)
        // this.setRecharge()
        // this.oldCPage2 = true
      }
      if (a === 2) {
        // this.currentPage = 0
        // that.CapitalRecordList=[]
        // this.RechargePage = 0
        // that.RechargeList=[]
        // this.CashOutPage = 0
        // that.CashOutList=[]
        // console.log(this.CashOutPage)
        // this.setCashOut()
        // this.oldCPage3 = true
      }
    },
    // 返回上级
    linkHome () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/my";
</style>



